
import axios from "axios"
import { userInfo } from "os"
import { useContext, useRef, useState } from "react"
import { Link } from "react-router-dom"
import useSWR from 'swr'
import { Food } from "./FoodCart"
import { useInput } from "./hooks"
import { UserInfoContext } from "./Manage"

const fetcher = (url: string) => axios.get(url).then(res => res.data)

export default function FoodManage() {
  var userInfo = useContext(UserInfoContext)

  var { data: foods, isLoading, mutate } = useSWR<Food[]>(() => `/api/restaurant/${userInfo!.id}/food`, fetcher)

  function onFoodUpdate(updatedFood: Food) {
    mutate(foods => {
      return foods!.map(it => {
        if (it.id == updatedFood.id) {
          return updatedFood
        } else {
          return it
        }
      })
    })
  }
  function onFoodDelete(food: Food) {
    mutate(foods => {
      return foods!.filter(it => it.id !== food.id)
    })
  }

  return <div>
    <h1></h1>
    <div><Link to="/manage/add-food"></Link></div>
    {isLoading
      ? '...'
      : foods && (
        <div>
          {
            foods!.map(food => {
              return <FoodManageItem key={food.id} food={food} onDelete={onFoodDelete} onChange={onFoodUpdate} />
            })
          }
        </div>
      )

    }
  </div>
}


type Props = {
  food: Food
  onChange?: (updatedFood: Food) => void | undefined
  onDelete?: (updatedFood: Food) => void | undefined
}

const FoodManageItem: React.FC<Props> = ({ food, onChange, onDelete }) => {
  var [isEditing, setIsEditing] = useState(false)
  var name = useInput(food.name)
  var desc = useInput(food.desc)
  var price = useInput(food.price)
  var category = useInput(food.category)
  var img = useRef<any>()

  var userInfo = useContext(UserInfoContext)

  async function save() {
    var formData = new FormData()

    formData.append('name', String(name.value))
    formData.append('desc', String(desc.value))
    formData.append('price', String(price.value))
    formData.append('category', String(category.value))
    formData.append('img', img.current.files[0])

    var updatedFood = (await axios.put(`/api/restaurant/${userInfo!.id}/food/${food.id}`, formData)).data // 
    setIsEditing(false)
    if (typeof onChange === 'function') {
      onChange(updatedFood)
    }
  }

  async function trunOff() {
    var formData = new FormData()
    formData.append('status', 'off')
    var updatedFood = (await axios.put(`/api/restaurant/${userInfo!.id}/food/${food.id}`, formData)).data
    if (typeof onChange === 'function') {
      onChange(updatedFood)
    }
  }
  async function turnOn() {
    var formData = new FormData()
    formData.append('status', 'on')
    var updatedFood = (await axios.put(`/api/restaurant/${userInfo!.id}/food/${food.id}`, formData)).data
    if (typeof onChange === 'function') {
      onChange(updatedFood)
    }
  }
  async function deleteFood() {
    var updatedFood = (await axios.delete(`/api/restaurant/${userInfo!.id}/food/${food.id}`)).data
    if (typeof onChange === 'function') {
      onChange(updatedFood)
    }
  }

  if (isEditing) {
    return <div>
      <fieldset>
        <div>: <input type="text" {...name} /></div>
        <div>: <input type="text" {...desc} /></div>
        <div>: <input type="text" {...price} /></div>
        <div>: <input type="text" {...category} /></div>
        <div>: <input type="file" ref={img} /></div>
        <div>
          <button onClick={save}></button>
          <button onClick={() => setIsEditing(false)}></button>
        </div>
      </fieldset>
    </div>
  } else {
    return <div>
      <fieldset>
        <legend>{food.name}</legend>
        <img style={{ float: 'left', width: 50, height: 50}} src={`/upload/${food.img}`} />
        <div>{food.price} </div>
        <div>{food.desc}</div>
        <div>{food.category}</div>
        <div>{food.status}</div>
        <div>
          <button onClick={() => setIsEditing(true)}></button>
          {food.status == 'on'
            ? <button onClick={trunOff}></button>
            : <button onClick={turnOn}></button>
          }
          <button onClick={deleteFood}></button>
        </div>
      </fieldset>
    </div>
  }
}
