import { Link, Outlet, useNavigate } from "react-router-dom"
import "./Manage.css"
import useSWR from 'swr'
import axios from 'axios'
import { createContext } from "react"

const fetcher = (url: string) => axios.get(url).then(res => res.data)

type UserInfo = {
  id: number,
  name: string,
  title: string,
}

export const UserInfoContext = createContext<UserInfo | null>(null)
UserInfoContext.displayName = 'UserInfoContext'

export default function Manage() {

  var { data: userInfo, isLoading } = useSWR('/api/userinfo', fetcher)

  var navigate = useNavigate()
  async function logout() {
    await axios.get('/api/logout')
    navigate('/')
  }

  return <div className="Manage">
    <aside className="Manage-sidebar">
      <div><Link to="/manage/orders"></Link></div>
      <div><Link to="/manage/foods"></Link></div>
      <div><Link to="/manage/desks"></Link></div>
      <div><button onClick={logout}></button></div>
    </aside>
    <UserInfoContext.Provider value={userInfo}>
      <main className="Manage-main">
        <Outlet />
      </main>
    </UserInfoContext.Provider>
  </div>
}
