import axios from 'axios'
import { useEffect, useRef, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import useSWR from 'swr'
import './FoodCart.css'
import { io } from 'socket.io-client'
import { useImmer } from 'use-immer'


const fetcher = (url: string) => axios.get(url).then(res => res.data)

export type Food = {
  category: string
  desc: string
  id: number
  img: string
  name: string
  price: number
  rid: number
  status: string
}

type FoodCountMap = {
  [index: number]: number
}

export default function FoodCart() {
  const params = useParams()

  const { data: foods, error, isLoading } = useSWR<Food[]>(`/api/menu/restaurant/${params.rid}`, fetcher)
  const { data: deskInfo } = useSWR(`/api/deskinfo?did=${params.did}`, fetcher)

  // {2: 3, 8: 1}
  // 23,81
  const [foodCount, setFoodCount] = useImmer<FoodCountMap>({})

  const navigate = useNavigate()
  const socket: any = useRef()

  useEffect(() => {
    socket.current = io({
      path: '/desk', //  ws://meituandianchan.com/desk
      query: {
        desk: 'desk:' + params.did
      }
    })

    // socketio
    socket.current.on('connect', () => {
      // 
      socket.current.emit('join desk', 'desk:' + params.did)
    })

    // 
    socket.current.on('cart food', (info: any) => {
      console.log('cart init', info)
      setFoodCount(foodCount => {
        for (var item of info) {
          foodCount[item.food.id] = item.amount
        }
      })
    })

    // 
    socket.current.on('new food', (info:any) => {
      console.log(info)
      setFoodCount(foodCount => {
        foodCount[info.food.id] = info.amount
      })
    })

    // 
    socket.current.on('placeorder success', (order: any) => {
      console.log(order)

      // navigate('/')
      navigate(`/r/${params.rid}/d/${params.did}/order-success`, {
        state: order
      })

      // history.push({
      //   pathname: `/r/${params.rid}/d/${params.did}/order-success`,
      //   state: order,
      // })

    })

    return () => { socket.current.close() }
  }, [])


  function plus(id: number) {
    var food = foods?.find(food => food.id == id)
    var originalCount = foodCount[id] ?? 0

    // setFoodCount(foodCount => {
    //   foodCount[id] = originalCount + 1
    // })

    // socket.io
    socket.current.emit('new food', {
      desk: 'desk:' + params.did,
      food,
      amount: originalCount + 1
    })
  }
  function minus(id: number) {
    var food = foods?.find(food => food.id == id)
    var originalCount = foodCount[id] ?? 0

    if (originalCount == 0) {
      return
    }

    // setFoodCount(foodCount => {
    //   foodCount[id] = originalCount - 1
    // })

    // socket.io
    socket.current.emit('new food', {
      desk: 'desk:' + params.did,
      food,
      amount: originalCount - 1
    })
  }

  function totalPrice() {
    return foods!.map(it => {
      var id = it.id
      var price = it.price
      var amount = foodCount[id] ?? 0
      return price * amount
    }).reduce((a, b) => a + b, 0)
  }

  async function placeorder() {
    await axios.post(`/api/restaurant/${params.rid}/desk/${params.did}/order`,{
      deskName: deskInfo.name,
      customCount: params.customerCount,
      totalPrice: totalPrice(),
      // foods: [{ food: {}, amount: 1 }, { food: {}, amount: 2 }],
      foods: foods!.filter(food => {
        var id = food.id
        var amount = foodCount[id] ?? 0
        return amount > 0
      }).map(food => {
        var id = food.id
        var amount = foodCount[id] ?? 0
        return {
          food, amount
        }
      })
    })
  }

  var [showCart, setShowCart] = useState(false)
  function toggle() {
    setShowCart(it => !it)
  }

  return <div className="FoodCart">
    <h1>{deskInfo?.title} - {deskInfo?.name}</h1>
    <h2></h2>
    {isLoading
      ? '...'
      : (
        <>
          <div className="FoodCart-list">
            {
              foods!.map((food) => {
                return (
                  <fieldset key={food.id} className="FoodCart-item">
                    <legend>{food.name}</legend>
                    <img src={`/upload/${food.img}`} alt={food.name} style={{ float: 'left' }} />
                    <div>{food.desc}</div>
                    <div>{food.price}</div>
                    <div>
                      <button disabled={food.status == 'off'} onClick={() => minus(food.id)}>-</button>
                      <span> { foodCount[food.id] ?? 0 } </span>
                      <button disabled={food.status == 'off'} onClick={() => plus(food.id)}>+</button>
                    </div>
                  </fieldset>
                )
              })
            }
          </div>
          <footer className="FoodCart-footer">
            {showCart &&
              <div className="FoodCart-footer-details">
                {
                  foods!.filter(food => {
                    var id = food.id
                    var amount = foodCount[id] ?? 0
                    return amount > 0
                  }).map(food => {
                    var id = food.id
                    var amount = foodCount[id] ?? 0
                    return <div style={{display: 'flow-root'}} key={food.id}>
                      {food.name} | {amount} | {food.price * amount}

                      <span style={{float:'right'}}>
                        <button onClick={() => minus(food.id)}>-</button>
                        <span> { foodCount[food.id] ?? 0 } </span>
                        <button onClick={() => plus(food.id)}>+</button>
                      </span>
                    </div>
                  })
                }
              </div>
            }
            <button onClick={toggle}></button>
            <span>: {totalPrice()}</span>
            <button onClick={placeorder}></button>
          </footer>
        </>
      )
    }
  </div>
}
