import { useState } from "react"
import axios, { AxiosError } from "axios"
import { useNavigate } from "react-router-dom"
import { useInput } from "./hooks"

export default function Login() {
  var [captchaUrl, setCaptchaUrl] = useState('/api/captcha')

  var name = useInput('')
  var password = useInput('')
  var captcha = useInput('')

  var navigate = useNavigate()

  function changeCaptcha() {
    setCaptchaUrl('/api/captcha' + '?t=' + Date.now())
  }

  async function submit() {
    try {
      var res = await axios.post('/api/login', {
        name: name.value,
        password: password.value,
        captcha: captcha.value,
      })
      console.log(res)
      navigate('/manage/orders')
    } catch (e) { // axiosisAxiosError
      if ((e as AxiosError).isAxiosError) {
        alert((e as any).response.data.msg)
      } else {
        alert(String(e))
        throw e
      }
    }
  }

  return <div>
    <div></div>
    <div><input {...name} /></div>
    <div></div>
    <div><input {...password} /></div>
    <div><img onClick={changeCaptcha} src={captchaUrl} /></div>
    <div></div>
    <div><input {...captcha} /></div>
    <div><button onClick={submit}></button></div>
  </div>
}
