import axios from "axios"
import { useContext, useRef } from "react"
import { useNavigate } from "react-router-dom"
import { useInput } from "./hooks"
import { UserInfoContext } from "./Manage"



export default function AddFood() {
  var userInfo = useContext(UserInfoContext)
  var name = useInput('')
  var desc = useInput('')
  var price = useInput(0)
  var category = useInput('')
  var name = useInput('')
  var img = useRef<any>()

  var navigate = useNavigate()

  async function submit() {
    var formData = new FormData()

    formData.append('name', String(name.value))
    formData.append('desc', String(desc.value))
    formData.append('price', String(price.value))
    formData.append('category', String(category.value))
    formData.append('status', 'off')
    formData.append('img', img.current.files[0])

    var newFood = (await axios.post(`/api/restaurant/${userInfo!.id}/food`, formData)).data // 
    navigate('/manage/foods')
  }

  return (
    <div>
      <h2></h2>
      <fieldset>
        <div><input type="text" {...name} /></div>
        <div><input type="text" {...desc} /></div>
        <div><input type="text" {...price} /></div>
        <div><input type="text" {...category} /></div>
        <div><input type="file" ref={img} /></div>
        <div>
          <button onClick={submit}></button>
        </div>
      </fieldset>
    </div>
  )
}
