import useSWR from 'swr'
import axios from 'axios'
import dayjs from 'dayjs'
import { Food } from './FoodCart'
import { useContext, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import { io } from 'socket.io-client'
import { UserInfoContext } from './Manage'

const fetcher = (url: string) => axios.get(url).then(res => res.data)

type OrderFoodEntry = {
  amount: number
  food: Food
}

type Order = {
  customCount: number
  deskName: string
  details: OrderFoodEntry[]
  did: number
  id: number
  rid: number
  status: "pending" | "confirmed" | "completed"
  timestamp: string
  totalPrice: number
}

export default function OrderManage() {
  var userInfo = useContext(UserInfoContext)
  var { data: orders, isLoading, mutate } = useSWR<Order[]>('/api/restaurant/1/order', fetcher)

  useEffect(() => {
    if (userInfo) {
      var socket = io({
        path: '/restaurant',
        query: {
          restaurant: 'restaurant:' + userInfo.id
        }
      })

      socket.on('connect', () => {
        socket.emit('join restaurant', 'restaurant:' + userInfo!.id)
      })

      socket.on('new order', order => {
        mutate(orders => {
          if (orders) {
            return [order, ...orders]
          }
        })
      })

      return () => {
        socket.close()
      }
    }
  }, [userInfo])



  function printOrder(order: Order) {
    var iframe = document.createElement('iframe')
    document.body.append(iframe)
    // var win = window.open()
    var win = iframe.contentWindow
    var doc = win!.document
    for (var entry of order.details) {
      doc.write(`
        <div>
          ${entry.food.name} - ${entry.amount} - ${entry.food.price * entry.amount}
        </div>
      `)
    }
    win?.print()
    document.body.removeChild(iframe)
  }

  async function deleteOrder(order: Order) {
    try {
      await axios.delete(`/api/restaurant/${userInfo!.id}/order/${order.id}`)
      mutate(orders => {
        return orders!.filter(it => it.id !== order.id)
      })
    } catch (e) {
      alert('')
    }
  }

  async function confirmOrder(order: Order) {
    try {
      var updatedOrder: Order = (await axios.put(`/api/restaurant/${userInfo!.id}/order/${order.id}/status`, {
        status: 'confirmed'
      })).data
      mutate(orders => {
        return orders!.map(it => {
          if (it.id == updatedOrder.id) {
            return updatedOrder
          } else {
            return it
          }
        })
      })
    } catch (e) {
      alert('')
    }
  }
  async function completeOrder(order: Order) {
    try {
      var updatedOrder: Order = (await axios.put(`/api/restaurant/${userInfo!.id}/order/${order.id}/status`, {
        status: 'completed'
      })).data
      mutate(orders => {
        return orders!.map(it => {
          if (it.id == updatedOrder.id) {
            return updatedOrder
          } else {
            return it
          }
        })
      })
    } catch (e) {
      alert('')
    }
  }

  return <div>
    <h1></h1>
    {isLoading
      ? '...'
      : (
        <div>
          {
            orders!.map(order => {
              return (
                <fieldset key={order.id}>
                  <legend>{order.deskName}</legend>
                  <div>{order.totalPrice}</div>
                  <div>{order.customCount}</div>
                  <div>{order.status}</div>
                  <div>{dayjs(order.timestamp).format('YYYY-MM-DD HH:mm:ss')}</div>
                  <div className="operations">
                    <button onClick={() => printOrder(order)}></button>
                    <button onClick={() => confirmOrder(order)}></button>
                    <button onClick={() => completeOrder(order)}></button>
                    <button onClick={() => deleteOrder(order)}></button>
                  </div>
                </fieldset>
              )
            })
          }
        </div>
      )
    }
  </div>
}
