import { useEffect, useState } from "react"
import "./LandingPage.css"
import classnames from 'classnames'
import { useNavigate, useParams } from "react-router-dom";
import axios from 'axios'
import useSWR from 'swr'

const fetcher = (url:string) => axios.get(url).then(res => res.data)

export default function LandingPage() {
  const [customerCount, setCustomerCount] = useState(0)
  const navigate = useNavigate() // 
  const params = useParams()

  const { data, isLoading } = useSWR(`/api/deskinfo?did=${params.did}`, fetcher)

  function startOrdering() {
    // go /r/1/d/1/c/3
    if (customerCount == 0) {
      alert('')
      return
    }

    navigate(`/r/${params.rid}/d/${params.did}/c/${customerCount}`)
  }

  return <div>
    <h1>{isLoading ? '' : `${data.title} - ${data.name}`}</h1>
    
    <ul className="LandingPage-customer-count-list">
      {
        new Array(8).fill(0).map((it, idx) => {
          return <li className={classnames({active: customerCount == idx + 1})} key={idx} onClick={() => setCustomerCount(idx + 1)}>{idx + 1}</li>
        })
      }
    </ul>
    <button onClick={startOrdering}></button>
  </div>
}
